//メディアクエリ---------------------------//
$breakpoints: (
	s: "(min-width: 480px)",
	m: "(min-width: 600px)",
	l: "(min-width: 960px)",
	xl: "(min-width: 1280px)",
);

@mixin media($breakpoint) {
	@media #{map-get($breakpoints, $breakpoint)} {
		@content;
	}
}

//アニメーション------------------------------//
@mixin animation(
  $name,
  $duration: 1s,
  $timing-function: ease,
  $delay: 0s,
  $iteration-count: 1,
  $direction: normal,
  $fill-mode: forwards
) {
  animation: {
    name: $name;
    duration: $duration;
    timing-function: $timing-function;
    delay: $delay;
    iteration-count: $iteration-count;
    direction: $direction;
    fill-mode: $fill-mode;
  }
}