@use '../modules/variables' as v;

/*
GENERAL STYLING
================================*/
html {
	font-size: 100%;
}
body {
	color: v.$text;
	background-color: v.$bg;
	font-family: "Bree Serif", sans-serif;
}

/*
COMMON
====================================*/
p {
	line-height: 1.7;
}
img {
	max-width: 100%;
}
a {
	&:hover {
		color: #0bd;
	}
}

/*
HEADER
=================================*/
.header {
	position: relative;
	width: 100vw;
	height: 90vh;
	margin-bottom: 0.5rem;
	overflow: hidden;
	&__text {
		position: absolute;
		top: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: v.$text;
		z-index: 20;
	}
	&__title {
		font-size: 2rem;
	}
	&__name {
		font-size: 4rem;
		margin-bottom: 1rem;
	}
	&__link {
		font-size: 1.25rem;
	}
	&__img-box {
		position: relative;
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		z-index: 10;
	}
	&__img > img {
		object-fit: cover;
		object-position: center left;
	}
	&__text,
	&__img-box &__img > img {
		width: 100vw;
		height: 90vh;
	}
}

/*
GRID
========================================================*/
.grid {
	width: 94vw;
	margin: 0 auto 3vw;
	display: grid;
	gap: 2vw;
	grid-template-columns: repeat(2, 46vw);
	grid-template-rows: repeat(4, 46vw);
	&__intro {
		color: #949087;
		font-weight: bold;
		padding: 2rem;
		grid-column: 1/3;
	}
	&__text-box > h2 {
		font-size: 1.375rem;
		margin-bottom: 1rem; 
		& span {
			color: #ab0f50;
		}
	}
	&__text-box > p {
		margin-left: 1rem;
	}
	&__item {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}
	&__fin {
		position: relative;
		filter: grayscale(60%);
		grid-column: 1/3;
		&::before {
			content: "Relaxing...";
			font-family: "Helvetica LT W01 Roman";
			font-weight: 300;
			letter-spacing: 2px;
			color: #fff;
			font-size: 24px;
			line-height: 1;
			opacity: 1;
			position: absolute;
			top: calc(50% - 13px);
			left: 0;
			width: 100%;
			text-align: center;
			z-index: 100;
		}
	}
}

/*
FOOTER
=======================================*/
.footer {
	text-align: center;
	padding-bottom: 3rem;
	color: #949087;
	&__link {
		margin-bottom: 0.5rem;
	}
}
