@use './mixin' as m;

/*
文字アニメーション
===================================*/
.animate-title,
.tween-animate-title {
	opacity: 0;
	&.inview {
		opacity: 1;
		& .char {
			display: inline-block;
		}
	}
}

.animate-title.inview .char {
	@include m.animation(
		$name: kf-animate-chars,
		$duration: 0.5s,
		$timing-function: cubic-bezier(0.39, 1.57, 0.58, 1),
		$fill-mode: both
	);

	@for $i from 1 through 30 {
		&:nth-child(#{$i}) {
			animation-delay: $i * 0.04s;
		}
	}
}

@keyframes animate-chars {
	0% {
		opacity: 0;
		transform: translateY(-50%);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

/*
スクロールアニメーション
======================================*/

.down {
	position: relative;
	padding-top: 40px;
	& span {
		position: absolute;
		top: 100%;
		left: 50%;
		width: 24px;
		height: 24px;
		margin-left: -12px;
		border-left: 2px solid #fff;
		border-bottom: 2px solid #fff;
		-webkit-transform: rotate(-45deg);
		transform: rotate(-45deg);
		opacity: 0;
		@include m.animation(
			$name: sdb,
			$duration: 2s,
			$iteration-count: infinite
		)
	}
	& span:nth-of-type(1) {
		top: 96px;
		animation-delay: 0s;
		@include m.media(m) {
			top: 64px;
		}		
	}	
	& span:nth-of-type(2) {
		top: 128px;
		animation-delay: .15s;
		@include m.media(m) {
			top: 80px;
		}
	}	
	& span:nth-of-type(3) {
		top: 160px;
		animation-delay: .3s;
		@include m.media(m) {
			top: 96px;
		}
	}	
}

@keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
