@use '../modules/mixin' as m;
@use '../modules/variables' as v;

@include m.media(m) {
	/*
  DESKTOP SIZE
  ================================*/

	/*== Header ==*/
	.header {
		&__img > img {
			object-position: center top;
		}
	}

	/*== Grid ==*/
	.grid {
		width: 95vw;
		gap: 1vw;
		grid-template-columns: repeat(3, 31vw);
		grid-template-rows: repeat(3, 31vw);
		&__intro {
			position: relative;
			grid-column: 1/3;
			transition: .3s;
			&:hover {
				background-color: #f6efdb;
			}
		}
		&__text-box {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		&__text-box > h2 {
			font-size: 2rem;
			margin-bottom: 2rem;
		}
		&__text-box > p {
			margin-left: 1rem;
		
		}
		&__pattern {
			position: absolute;
			z-index: 1;
			background-size: auto auto;
			background-image: repeating-linear-gradient(
				0deg,
				transparent,
				transparent 2px,
				v.$bg 2px,
				v.$bg 4px
			);
		}
		&__man {
			grid-row: 2/4;
		}
		&__item {
			// height: 245px;
			transition: transform 0.3s ease;
			&:hover {
				filter: grayscale(60%);
				transform: scale(1.05);
			}
		}
		&__fin {
			
			grid-column: 1/4;
			pointer-events: none;
		}
	}
}
