@use './mixin' as m;

/*
スライドアニメーション
=================================*/

.cover-slide {
	position: relative;
	&::after {
		content: "";
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		// background-color: #f7f7f7;
		background-color: #f6efdb;
		opacity: 0;
	}
	&.inview {
		&::after {
			opacity: 1;
			@include m.animation(
				$name: cover-slide,
				$duration: 1.8s,
				$timing-function: ease-in-out
			);
		}
	}
}

