@keyframes cover-slide {
	0% {
		transform-origin: left;
		transform: scaleX(0);
	}
	50% {
		transform-origin: left;
		transform: scaleX(1);
	}
	50.1% {
		transform-origin: right;
		transform: scaleX(1);
	}
	100% {
		transform-origin: right;
		transform: scaleX(0);
	}
}

.image-zoom {
	opacity: 0;
	.inview & {
		opacity: 1;
		@include m.animation(
			$name: img-show,
			
			$duration: 1.6s,
			$timing-function: ease-in-out,
      $fill-mode: none
		);
	}
}

@keyframes img-show {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	50.1% {
		opacity: 1;
	}
	100% {
		opacity: 1;
	}
}

/*
フェードイン
================================*/
.fade-in {
	@include m.animation(
		$name: anime,
		$duration: 18s,
		$iteration-count: infinite
	);

	@for $i from 2 through 6 {
		&:nth-child(#{$i}) {
			animation-delay: $i * 6s - 6;
		}
	}
}

@keyframes anime {
	0% {
		opacity: 0;
	}
	33% {
		opacity: 1;
	}
	67% {
		opacity: 0;
	}
	78% {
		opacity: 0;
		transform: scale(1.2);
		z-index: 9;
	}
	100% {
		opacity: 0;
	}
}

